<style lang="css">
	@import '../../node_modules/bootstrap/dist/css/bootstrap.min.css';
	@import '../../node_modules/font-awesome/css/font-awesome.min.css';
	@import '../../node_modules/notie/dist/notie.css';
	@import '../../node_modules/hint.css/hint.min.css';


	* {
		border-radius: 0 !important;

	}

	html, body {
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	body {
		background-image: url("data:image/gif;base64,");

	}

	.white-link {
		color: #ffffff;
		text-decoration: underline;

	}

	.white-link:hover {
		color: #ffffff;
		text-decoration: none;

	}

	.app {
		height: 100%;
		display: flex;

	}

	.content {
		height: 100%;
		width: 100%;
		padding-top: 5em;

	}

	.content.no-sidebar, .content.no-sidebar .container-fluid {
		padding: 0;

	}

	@media (min-width: 48em) {
		.content {
			padding-top: 1em;
			padding-left: 64px;

		}
	}
</style>
<template>
	<div class="app">
		<div class="sidebar-wrapper" v-show="user.authenticated">
			<sidebar></sidebar>
		</div>
		<div class="content" v-bind:class="{ 'no-sidebar': !user.authenticated }">
			<div class="container-fluid">
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>
<script type="text/ecmascript-6">
	import 'bootstrap/dist/js/bootstrap.js';
	import Sidebar from './sidebar';
	import auth from 'services/auth.service';
	import store from 'vuex/store';

	export default {
		data() {
			return {
				user: auth.user
			};
		},
		components: {
			Sidebar
		},
		store
	};
</script>



